.stars {
  &__star {
    height: 15px;
    width: 15px;
    margin-right: 4px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url(../images/star-active.svg);
  }

  &__star:nth-child(n + 5) {
    background-image: url(../images/star.svg);
  }
}
